import tab from "../../components/tab.vue";
import firstNav from "../../components/first-nav.vue";
import videoList from "../../components/videoList.vue";

export default {
	components: {
		tab,
		firstNav,
		videoList
	},
	data() {
		return {
			list: []
		}
	},
	onLoad() {
		this.getVideos();
	},
	methods: {
		getVideos() {
			uni.request({
				url: 'http://localhost:8088/json/videos.json', 
				success: (res) => {
					this.list = res.data.list;
				}
			});
		}
	}
}
